import { mapState} from 'vuex'
export default {
	data() {
		return {
			lis_num:''
		};
	},
	computed:{
		...mapState({
			sessionKey:state=>state.sessionKey,
			cardtype:state=>state.cardType
		})
	},
	methods:{
		sendCardType(type){
			this.$store.commit('getCardType',type)
		},
		formSubmit(e){
			if(!this.sessionKey) return uni.showToast({
				title:'请先完成授权!',
				icon:'none'
			})
			let formId=e.detail.formId;
			if(this.lis_num=='') return uni.showToast({
				title:'不能为空',
				icon:'none'
			})
			this.$api.listen({
				sessionKey:this.sessionKey,
				formId:formId,
				lisType:this.cardtype,
				lisNum:this.lis_num
			}).then(res=>{
				uni.showToast({
					title:res.msg,
					icon:'none'
				})
				this.lis_num='';
			})
		}
	}
}
